import dbConnect from '../../../lib/db';
import Menu from '../../../models/Menu';

export async function GET(request) {
    try {
        console.log('Connecting to DB for menu...');
        await dbConnect();
        const { searchParams } = new URL(request.url);
        const category = searchParams.get('category');

        const filters = {};
        if (category) filters.category = category;

        console.log('Fetching menu items with filters:', filters);
        const items = await Menu.find(filters);
        console.log(`Successfully fetched ${items.length} menu items.`);
        return new Response(JSON.stringify(items), {
            status: 200,
            headers: { 'Content-Type': 'application/json' },
        });
    } catch (error) {
        console.error('Error in /api/menu:', error);
        return new Response(JSON.stringify({ error: error.message }), {
            status: 500,
            headers: { 'Content-Type': 'application/json' },
        });
    }
}
